<template>
    <nav class="nav">
      <div class="nav-left">
        <router-link class="nav-item is-brand" :to="'/'">Swagger Server</router-link>
      </div>
      <div class="nav-center">
        <router-link class="nav-item" :to="'/'">Home</router-link>
        <a class="nav-item" :href="api.scheme + '://' + api.host + ':' + api.port + '/static/swagger/editor'">Editor</a>
        <a class="nav-item" :href="api.scheme + '://' + api.host + ':' + api.port + '/static/swagger/ui'">UI</a>
        <router-link class="nav-item" :to="'/file'">FileManager</router-link>
      </div>
      <div class="nav-right nav-menu">
        <span class="nav-item">
          <router-link class="button" :to="'/mypage'">
            <span>MyPage</span>
          </router-link>
          <a class="button is-primary" :href="api.scheme + '://' + api.host + ':' + api.port + api.uri + '/auth/user/login'">
            <span id="topmenu-login">Auth</span>
          </a>
        </span>
      </div>
    </nav>
</template>

<script>
import config from '../../config'
import FetchBase from './Modules/FetchBase'

export default {
  name: 'TopMenu',
  mixins: [FetchBase],
  mounted: function () {
    this.basicRequest(
      'GET',
      this.api.scheme + '://' + this.api.host + ':' + this.api.port + this.api.uri + '/user/info',
      null,
      function(json) {
        if(json.result == "OK") {
          document.querySelector("#topmenu-login").innerHTML = "Logout"
        } else {
          document.querySelector("#topmenu-login").innerHTML = "Login"
        }
      }
    )
  },
  data () {
    return {
      api: config.dev.api
    }
  }
}
</script>



